.navLink{
    -webkit-user-select: none;
            user-select: none;
    color:#fff;
    display:flex;
    align-items:center;
    padding: 0 1rem;
    cursor: pointer;
    margin: 0;
    height: 100%;
    text-decoration: none;
}
.navLink:hover{
    text-decoration: none;
    color: #15cdfc;
}
.navLink.active {
    color: #15cdfc;
}
.nav{
    background: #000;
    height: 80px;
    display: flex;
    z-index: 10;
}
.logoSmall{
    height: 50px;
    width: 50px;
    border-radius: 50%;
}
.bars{
    display: none;
    color: #fff;
}
.navMenu{
    display:flex;
    align-items:center;
    justify-content: center;
    margin-right: -24px;
    width: calc(100% - 82px);
    color:#fff;
}
.navMenu p ul{
    display: none;
    flex-direction: column;
    -webkit-user-select: none;
            user-select: none;
    position: absolute;
    top: 80px;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
    z-index: 100;
    background-color: #000;
    color: #fff;
    border-radius: 0 0 15px 15px;
    padding: 0 20px;
    cursor: default;
}
.navMenu p ul a{
    text-decoration: none;
    color: #fff;
    cursor: pointer;
    height: 50px;
    display: flex;
    justify-content: left;
    align-items: center;
}
.navMenu p:hover ul{
    display: flex;

}
.navMenu p ul a:hover{
    color: #15cdfc;
}



.containerInfoContact{
    font-family: Arial, Helvetica, sans-serif;
    visibility: hidden;
    opacity: .0;
    position: fixed;
    inset: 0 0 0 0;
    z-index: 99;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .8);
    transition: all 0.3s;
    display: flex;
    justify-content: center;
    align-items: center;
}
.activecontainerInfoContact{
    visibility: visible;
    opacity: 1;
}
.infoContact{
    position: absolute;
    background-color: rgb(245, 245, 245);
    width: 560px;
    height: -webkit-max-content;
    height: max-content;
    border-radius: 20px;
    padding: 10px 10px;
    z-index: 100;
    opacity: .0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.activeinfoContact{
    opacity: 1;
}
.titleContact{
    display: flex;
    padding: 5px 0 15px 0;
}
.logoSmallContact{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.titleContact h6{
    display: flex;
    font-size: 1.5rem;
    font-weight: 500;
    text-align: center;
    margin: auto;
    padding-right: 30px;
}
.Hicon_close{
    position: absolute;
    right: 10px;
    width: 25px;
    height: 25px;
    cursor: pointer;
}
.Hicon_close:hover{
    color: red;
}
.contactanos{
    list-style: none;
}
.contactos{
    font-size: 1.2rem;
    padding: 5px 0;
}
.contactos a{
    color: #000;
    text-decoration: none;
    display: flex;
}
.contactos:nth-child(4) a .iconContact{
    font-size: 1.5rem;
}
.iconContact{
    margin-right: 5px;
}
.contactos:nth-child(1):hover a{
    color: #ff0150;
}
.contactos:nth-child(2):hover a{
    color: #25D366;
}
.contactos:nth-child(3):hover a{
    color: #039237;
}
.contactos:nth-child(4):hover a{
    color: #f65240;
}
@-webkit-keyframes movimiento {
    0%{-webkit-transform: translateY(10px);transform: translateY(10px);}
    100%{-webkit-transform: translateY(-30px);transform: translateY(-30px);
    Color: transparent;}
}
@keyframes movimiento {
    0%{-webkit-transform: translateY(10px);transform: translateY(10px);}
    100%{-webkit-transform: translateY(-30px);transform: translateY(-30px);
    Color: transparent;}
}
.container_index{
    width: 100%;
}
.profile{
    margin: auto;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    overflow: hidden;
    width: 300px;
    height: 400px;
    max-width: 300px;
    max-height: 400px;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 5px 18px rgba(0, 0, 0, 0.6);
}
.profile .profile-image{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 2;
    transition: .5s;
}
.profile-image img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.social-icons{
    position: absolute;
    display: flex;
    grid-gap: 12px;
    gap: 12px;
    z-index: 3;
    bottom: 170px;
}
.social-icons li{
    list-style: none;
}
.social-icons li a{
    position: relative;
    display: block;
    display: grid;
    place-items: center;
    background: #fff;
    font-size: 23px;
    font-weight: bold;
    width: 40px;
    height: 40px;
    border-radius: 4px;
    transition: .2s;
    -webkit-transform: translateY(32px);
            transform: translateY(32px);
    opacity: 0;
}
.social-icons li a:hover{
    background: #2C2C2C;
    transition: .2s;
}
.social-icons li a svg{
    color: #2C2C2C;
    transition: .2s;
}
.social-icons li a:hover svg{
    color: white;
    transition: 0.6s;
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
.profile:hover .social-icons li a{
    opacity: 1;
}
.profile:hover li:nth-child(2) a{
    transition-delay: 0.1s;
}
.profile:hover li:nth-child(3) a{
    transition-delay: 0.2s;
}
.profile:hover li:nth-child(4) a{
    transition-delay: 0.3s;
}
.profile-name{
    position: absolute;
    bottom: -100%;
    left: 0;
    background-color: #fff;
    width: 100%;
    padding: 16px;
    color: #333;
    text-align: left;
    z-index: 10;
    transition: all .6s;
}
.profile:hover .profile-name{
    bottom: 0;
}
.profile-name h2{
    margin-bottom: 8px;
}
.profile:hover .quien_soy{
    bottom: -100%;
}
.quien_soy{
    z-index: 9;
    position: absolute;
    bottom: 0;
    height: 40%;
    width: 100%;
    background-color: rgba(0, 0, 0, .3);
    font-size: 2.5rem;
    color: #fff;
    transition: all .6s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.quien_soy svg{
    rotate: -20deg;
}
.closeNavmenu{
    display: none;
}
@media screen and (max-width: 768px) {
    .bars{
        display: block;
        margin: auto;
        margin-right: 20px;
        font-size: 2.2rem;
        cursor:pointer;
    }
    .navMenu {
        background-color: #0c0d0e;
        position: fixed;
        height: 100vh ;
        width: 100%;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        z-index: 10;
        top: -100vh;
        transition: 0.8s;
    }
    .activeNavmenu{
        top: 0;
    }
    .closeNavmenu {
        display: block;
        top: 20px;  
        right: 20px;        
        text-align: right;
        margin: 0 0 0 auto;
        width: 40px;
        height: 40px;   
        color: #fff;
    }
    .navMenu a,.navMenu p {
        text-decoration: none;
        position: relative;
        height: 120px;
        padding: 0;
        font-size: 20px;
        justify-content: center;

    }
    .navMenu p ul {
        background-color: #0c0d0e;
        top: 120px;
        height: -webkit-max-content;
        height: max-content;
        -webkit-transform: translateX(0);
                transform: translateX(0);
        padding: 0;
    }
    .navMenu p ul a{
        height: 50px;
        display: flex;
        justify-content: left;
        align-items: center;
    }
    .navMenu p ul a {
        color: #fff;
    }
    
}

@media screen and (max-width: 590px) {
    .infoContact{
        width: 95%;
    }
    .social-icons{
        bottom: 190px;
    }
}

@media screen and (max-width: 350px) {
    .profile{
        width: 85vw;
        height: 113vw;
        max-width: 30vh;
        max-height: 40vh;
    }
}
